.header {
   width: 100%;
   height: 64px;
}

.container {
   display: flex;
   justify-content: space-between;
   max-width: 1200px;
   height: 100%;
   margin: 0 auto;
}

.logo {
   display: flex;
}

.icon {
   height: 64px;
   width: 105px;
   background-image: url('./../../assets/img/logo.png');
   background-size: cover;
   background-repeat: no-repeat;
}

.logoText {
   color: rgb(182, 182, 182);
   transform:skew(15deg);
   font-size: 48px;
}

.search {
   height: 100%;
   width: 140px;
   margin-right: 30px;
}

